@import "../../global.scss";

.portfolio {
  background-image: linear-gradient(rgb(66, 66, 66), rgb(158, 158, 158));
  display: flex;
  flex-direction: column;
  align-items: center;

  @include mobile {
    height: 100vh;
  }

  .title-only {
    overflow: hidden;
    margin: 40px 40px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include mobile {
      margin: 0 0;
    }

    h1 {
      align-items: center;
      color: whitesmoke;
      box-sizing: border-box;
      display: block;
      line-height: normal;
      position: static;
      padding: 40px;
      padding-bottom: 0;

      .me {
        color: $mainColor;
      }
    }
    .underline {
      height: 6px;
      width: 200px;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: $mainColor;
    }
  }

  ul {
    margin: 10px;
    padding: 0;
    list-style: none;
    display: flex;

    @include mobile {
      margin: 10px 0;
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  .container {
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;

    @include mobile {
      width: 100%;
    }

    .item {
      width: 220px;
      height: 150px;
      border-radius: 20px;
      border: 1px solid lightgray;
      margin: 10px 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      position: relative;
      transition: all 0.5s ease;
      cursor: pointer;

      @include mobile {
        width: 130px;
        height: 100px;
      }

      h3 {
        position: absolute;
        font-size: 20px;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 1;
      }

      &:hover {
        background-color: $mainColor;
        img {
          opacity: 0.2;
          z-index: 0;
        }
      }
    }
  }
}
